import React, { Component } from 'react';
import { Link } from 'react-router-dom'
import './Registro.css';
import Citi from "../../assets/Image 3.png"

class Registro extends Component {
  render() {
    return (
      <div className="container-registro">
        <div className="gradient-brand">
          <figure>
            <img src={Citi} />
          </figure>
          <div className="registro">
            <span>NÚMERO DE CUENTA</span>
            <input type="text" />
            <span>CELULAR</span>
            <input type="text" />
 
            {/* <button className=“btn-secondary”>CONTINUAR</button> */}
            <Link to="./home" className="text-center panel-register">
              <button className="secondary">CONTINUAR</button>
            </Link>
            <a href="" className="footer">
              Términos y condiciones
            </a>
          </div>
        </div>
      </div>
    );
  }
}

export default Registro;